.leaderboard {
    padding: 20px;
  }
  
.leaderboard h3 {
  margin-bottom: 15px;
}

  /* Заголовок */
  .leaderboard-title {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  /* Текущий пользователь */
  .current-user {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 8px;
    background-color: #192955;
  }
  
  .user-info {
    display: flex;
    align-items: center;
  }
  
  .user-icon {
    width: 35px;
    height: 35px;
    background-color: #2F4FA4;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 18px;
    margin-right: 15px;
  }
  
  .user-details {
    display: flex;
    flex-direction: column;
    text-align: left; /* Выровнять текст по левому краю */
  }
  
  .user-name {
    font-size: 16px;
  }
  
  .user-tokens {
    font-size: 14px;
    color: #ADC3FF;
  }
  
  .user-rank {
    font-size: 16px;
    color: #ADC3FF;
    text-align: right; /* Ранг будет выровнен по правому краю */
  }
  
  /* Топ-100 игроков */
  .top-players {
    margin-top: 20px;
  }
  
  .players-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .player-item {
    display: flex;
    justify-content: space-between; /* Имя слева, баланс справа */
    align-items: center;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 8px;
    border: #284286 2px solid;
  }
  
  .player-name, .player-tokens {
    text-align: left; /* Для имени и баланса — выравнивание текста влево */
  }
  
  .player-info {
    display: flex;
    align-items: center;
  }
  
  .player-icon {
    width: 35px;
    height: 35px;
    background-color: #2F4FA4;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 18px;
    margin-right: 15px;
  }
  
  .player-details {
    display: flex;
    flex-direction: column;
    text-align: left; /* Выровнять текст по левому краю */
  }
  
  .player-name {
    font-size: 16px;
  }
  
  .player-tokens {
    font-size: 14px;
    color: #ADC3FF;
  }
  
  /* Ранг игрока */
  .player-rank {
    font-size: 16px;
    color: #ADC3FF;
    display: flex;
    align-items: center;
    text-align: right; /* Выровнять ранг или медаль по правому краю */
  }
  
  .medal-icon {
    width: 20px;
    height: 20px;
    margin-left: 10px;
  }
  